
import {mapGetters, mapMutations, mapActions} from 'vuex';
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
components: {
XInput,
Group,
XHeader,
Flexbox,
FlexboxItem,
XButton,
Toast
},
methods: {
...mapGetters([

'login'
]),
login() {
this.$axios.post("/api/StaffManager/login", {UserJobNo:this.User.user,Password:this.User.password}).then((response) => {
if (response.data.flag == '1') {
console.info('login')
sessionStorage.setItem('user',JSON.stringify(response.data.data))

this.userinfo=response.data
this.$router.push({
path: '/HelloWorld'
})
}
else{
console.info('login fail')
this.showPositionValue=true
}
}).catch(

function (error) {
console.log(error)

}
)


}
},
data() {
return {
User: {
user: '',
password: ''
},
userinfo: [],
showPositionValue:false
}
}
}



