
import bus from '../script/eventBus.js'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Selector from 'vux/src/components/selector/index.vue'


export default {
components: {
PopupPicker,
Group,
Picker,
XButton,
Cell,
Flexbox,
FlexboxItem,
XTable,
XHeader,
Tab,
TabItem,
Scroller,
XDialog,
Selector
},
mounted() {
console.log('mounted')
this.staffmodel.user = JSON.parse(sessionStorage.getItem('user')).UserName
console.info(this.staffmodel.user)
this.$axios.post('/api/StaffManager/getOnlieInfo', { shift: this.staffmodel.shift[0], line: this.staffmodel.line, user: this.staffmodel.user }).then((response) => {
console.log(response.data)
if (response.data.flag == 1) {
console.log(this)
console.log(response.data.data[0].line)
this.staffmodel.line = response.data.data[0].line
this.onlinelist = response.data.data
this.online_num = response.data.data.length
console.log(this.onlinelist)

}
}).catch(
function (error) {
console.log(error)
}
);
this.getlinelist();
},
methods: {
onChange(val) {
console.log('val change', val)
},
onItemClick(status) {
if (status == '0') {
this.seen = 'OK'
}
if (status == '1') {
console.log("jinru")
this.seen = 'ERROR'
this.$axios.post('/api/StaffManager/getOnlieInfo', { shift: this.staffmodel.shift[0], line: this.staffmodel.line, user: this.staffmodel.user, status: '1' }).then((response) => {
console.info(response.data)

this.rolllist = response.data.data;
this.roll_num = response.data.data.length;

}
).catch(
function (error) {
console.log(error)
}
)
}
},
onShow() { },
onHide() {

},
stringcombine(str1, str2) {
return str1, str2
},
checkAll() {
console.info("123")
if (this.onlinelist.length > 0) {
this.onlinelist.forEach(element => {
element.check = true;
});

}
},
invertCheck() {

if (this.onlinelist.length > 0) {
this.onlinelist.forEach(element => {
element.check = !element.check;
});

}
},
checkNone() {
if (this.onlinelist.length > 0) {
this.onlinelist.forEach(element => {
element.check = false;
});

}
},
gowork() {

var list = []
var linelist = []
this.onlinelist.forEach((item, index) => {

if (item.check) {
list.push(item)
}
else {
linelist.push(item)

}
})
console.log(list)
if (list.length == 0)
return;
this.$axios.post('/api/StaffManager/goWork', list).then((response) => {

console.log(response.data)
if (response.data.flag == 1) {
console.info('test')
this.onlinelist = linelist
console.log(this.onlinelist)
}
}).catch(
function (error) {
console.log(error)
}
)

},
transwork() {
var templine = []
this.onlinelist.forEach(item => {
if (item.check) {

templine.push(item)
}
});
if (templine.length > 0)
this.showScrollBox = true
},
linecheck(obj) {
var checkline = [];
var uncheline = [];
console.log(obj);
this.onlinelist.forEach(item => {
if (item.check) {
item.new_line = obj
checkline.push(item)
}
else {
uncheline.push(item)
}
});
console.log(checkline);
this.tanspost(checkline, uncheline);

this.showScrollBox = false
},

getlinelist() {
this.$axios.post('api/StaffManager/getLine', {}).then((response) => {
console.log(response.data)
if (response.data.flag == 1) {
console.log("getline")
this.linelist = response.data.data
console.log(this.linelist)

}
}).catch(
function (error) {
console.log(error)
}
)

},
tanspost(obj1, obj2) {
console.log(obj1);
this.$axios.post('/api/StaffManager/transWork', obj1).then((response) => {
console.log(response.data)
if (response.data.flag == 1) {
console.info('tanspost')
this.onlinelist = obj2
console.log(this.onlinelist)
}
}).catch(
function (error) {
console.log(error)
}
)

}


},
directives: {
TransferDom
},
data() {
return {
staffmodel:
{
shift: ['白班'],
line: '',
user: '张三一'
},
shiftlist: [['白班', '晚班']],
offworklist: [],
onlinelist: [],
rolllist: [],
online_num: 0,
roll_num: 0,
seen: 'OK',
showScrollBox: false,
new_line: '',
linelist: []
}

}

}



